import React, { Component } from 'react';
import {
  Page,
  Navbar,
  List,
  ListItem,
  ListInput,
  Link,
  Tabs,
  Toolbar,
  Tab,
  Range,
  Block,
  Icon, Fab
} from 'framework7-react';
import { dict } from '../../Dict';
import ModelStore from "../../stores/ModelStore";
import * as MyActions from "../../actions/MyActions";
import SectionShow from "../../containers/sections/show"

export default class Layout extends Component {
  constructor() {
    super();
    this.getInstance = this.getInstance.bind(this);
    this.loadCalender = this.loadCalender.bind(this);
    this.pageAfterIn = this.pageAfterIn.bind(this);
    this.submit = this.submit.bind(this);
   
    
    this.state = {
      token: window.localStorage.getItem('token'),
      faculties: null,
      assets:  null,
      total_assets: null,
      resource: null,
      quiz: null,
      assignment: null,
      onlineCourseCount: null,
      onlineCourseDuration: null,
      activity: null,
      id: null,
      name: null,
      bbOnlineCourseCount: null,
      bbOnlineCourseDuration: null,
      meetingHistogram:  null,
      bbMeetingHistogram: null,
      meetingCount: null,
      bbMeetingCount: null,
      start:new Date() ,
      end:new Date() ,
      inactives: 0,
      all: 0
    }
  }

  componentWillMount() {
    ModelStore.on("got_instance", this.getInstance);
  }

  componentWillUnmount() {
    ModelStore.removeListener("got_instance", this.getInstance);
  }

  componentDidMount() {
    MyActions.getInstance('courses/sections', this.$f7route.params['sectionId'], this.state.token);
   // this.loadCalender();
  }

  submit() {
    console.log('submiting ...')
    MyActions.getInstance('courses/sections', this.$f7route.params['sectionId'], this.state.token, {start: this.state.start, end:this.state.end});
  }

  getInstance() {
    var section = ModelStore.getIntance()
    var klass = ModelStore.getKlass()
    if (section && klass === 'Section') {
      this.setState({
        faculties: section.section_faculties,
        assets: section.section_assets,
        id: section.id,
        name: section.name,
        quiz: section.most_quiz,
        start: new window.ODate(section.start),
        end: new window.ODate(section.end),
        total_assets: section.total_assets,
        resource: section.most_resource,
        assignment: section.most_assignment,
        activity: section.most_activity,
        onlineCourseCount: section.most_online_course_count,
        onlineCourseDuration: section.most_online_course_duration,
        bbOnlineCourseCount: section.most_bb_online_course_count,
        bbOnlineCourseDuration: section.most_bb_online_course_duration,
        meetingHistogram: section.meetings,
        meetingCount: section.meetings_count,
        bbMeetingHistogram: section.bb_meetings,
        bbMeetingCount: section.bb_meetings_count,
        inactives: section.inactives[0],
        all:  section.inactives[1],
      });
    }
    console.log(section)    
  }


  pageAfterIn() {
    setTimeout(
      () => this.loadCalender(), 
      5000
    );
    
  }

  loadCalender(){
    const self = this;
    const app = self.$f7;
    console.log(new Date())

    app.calendar.create({
      inputEl: '#start',
      value: [self.state.start],
      closeOnSelect: true,
      firstDay: 6,
      weekendDays: [4, 5],
      monthNames: ['فروردين', 'ارديبهشت', 'خرداد', 'تیر', 'مرداد', 'شهریور', 'مهر', 'آبان', 'آذر', 'دی', 'بهمن', 'اسفند'],
      dayNames: ['یکشنبه', 'دوشنبه', 'سه شنبه', 'چهارشنبه', 'پنج‌شنبه', 'جمعه', 'شنبه'],
      dayNamesShort: ['یک', 'دو', 'سه ', 'چهار', 'پنج‌', 'جمعه', 'شنبه'],
      on: {
        closed: function (c) {
          var start = new Date(c.value[0].a).valueOf()/ 1000
          self.setState({ start:start });
        }
      }
    });


    app.calendar.create({
      inputEl: '#end',
      closeOnSelect: true,
      value: [self.state.end],
      firstDay: 6,
      weekendDays: [4, 5],
      monthNames: ['فروردين', 'ارديبهشت', 'خرداد', 'تیر', 'مرداد', 'شهریور', 'مهر', 'آبان', 'آذر', 'دی', 'بهمن', 'اسفند'],
      dayNames: ['یکشنبه', 'دوشنبه', 'سه شنبه', 'چهارشنبه', 'پنج‌شنبه', 'جمعه', 'شنبه'],
      dayNamesShort: ['یک', 'دو', 'سه ', 'چهار', 'پنج‌', 'جمعه', 'شنبه'],
      on: {
        closed: function (c) {
          var end = new Date(c.value[0].a).valueOf()/ 1000
          self.setState({ end: end }, () => {
            self.submit()
          });
        }
      }

    });

  }



  render() {
    const { 
      id, faculties, assets, total_assets , quiz, name,
      resource, assignment, onlineCourseCount, onlineCourseDuration, 
      activity, bbOnlineCourseCount, bbOnlineCourseDuration, meetingHistogram,
      bbMeetingHistogram, meetingCount, bbMeetingCount, inactives, all
    } = this.state;
    return (
      <Page onPageAfterIn={this.pageAfterIn.bind(this)}>
        <Navbar title={dict.sections} backLink={dict.back} />
        <SectionShow 
          id={id} faculties={faculties} name={name} assignment={assignment} 
          onlineCourseCount={onlineCourseCount} activity={activity} 
          onlineCourseDuration={onlineCourseDuration} quiz={quiz} 
          resource={resource} assets={assets} total_assets={total_assets}
          bbOnlineCourseCount={bbOnlineCourseCount}
          bbOnlineCourseDuration={bbOnlineCourseDuration}
          meetingHistogram={meetingHistogram}
          bbMeetingHistogram={bbMeetingHistogram}
          meetingCount={meetingCount}
          bbMeetingCount={bbMeetingCount}
          inactives={inactives}
          all={all}
          />
      </Page>
    );
  }
}
