import React, { Component } from 'react';
import {
  Page,
  Navbar,
  List,
  ListItem,
  ListInput,
  Link,
  Tabs,
  Toolbar,
  Tab,
  Range,
  Block,
  Icon, Fab
} from 'framework7-react';
import { dict } from '../../Dict';
import ModelStore from "../../stores/ModelStore";
import * as MyActions from "../../actions/MyActions";
import FacultyInactive from "../../containers/faculties/inactive"

export default class Layout extends Component {
  constructor() {
    super();
    this.getInstance = this.getInstance.bind(this);
    
    this.state = {
      token: window.localStorage.getItem('token'),
      courses: null,
    }
  }

  componentWillMount() {
    ModelStore.on("got_instance", this.getInstance);
  }

  componentWillUnmount() {
    ModelStore.removeListener("got_instance", this.getInstance);
  }

  componentDidMount() {
    MyActions.getInstance('courses/inactives', this.$f7route.params['facultyId'], this.state.token);
  }


  getInstance() {
    var faculty = ModelStore.getIntance()
    var klass = ModelStore.getKlass()
    if (faculty && klass === 'Inactive') {
      this.setState({
        courses: faculty.courses,
      });
    }
    console.log(faculty)    
  }

 


  render() {
    const { 
      courses,
    
    } = this.state;
    return (
      <Page >
        <Navbar title={dict.faculties} backLink={dict.back} />
        <FacultyInactive
          courses={courses} 
          />
      </Page>
    );
  }
}
