import React, { Component } from 'react';
import {
  Page,
  Navbar,
  List,
  ListItem,
  ListInput,
  Link,
  Tabs,
  Toolbar,
  Tab,
  Range,
  Block,
  Icon, Fab
} from 'framework7-react';
import { dict } from '../../Dict';
import ModelStore from "../../stores/ModelStore";
import * as MyActions from "../../actions/MyActions";
import FacultyShow from "../../containers/faculties/show"

export default class Layout extends Component {
  constructor() {
    super();
    this.getInstance = this.getInstance.bind(this);
    this.pageAfterIn = this.pageAfterIn.bind(this);
    this.submit = this.submit.bind(this);
    
    this.state = {
      token: window.localStorage.getItem('token'),
      courses: null,
      assets:  null,
      total_assets: null,
      resource: null,
      quiz: null,
      assignment: null,
      onlineCourseCount: null,
      onlineCourseDuration: null,
      activity: null,
      id: null,
      name: null,
      bbOnlineCourseCount: null,
      bbOnlineCourseDuration: null,
      meetingHistogram:  null,
      bbMeetingHistogram: null,
      meetingCount: null,
      bbMeetingCount: null,
      start:new Date() ,
      end:new Date() ,
      inactives: 0,
      all: 0
    }
  }

  componentWillMount() {
    ModelStore.on("got_instance", this.getInstance);
  }

  componentWillUnmount() {
    ModelStore.removeListener("got_instance", this.getInstance);
  }

  componentDidMount() {
    MyActions.getInstance('courses/faculties', this.$f7route.params['facultyId'], this.state.token);
  }

  submit() {
    console.log('submiting ...')
    MyActions.getInstance('courses/faculties', this.$f7route.params['facultyId'], this.state.token, {start: this.state.start, end:this.state.end});
  }

  getInstance() {
    var faculty = ModelStore.getIntance()
    var klass = ModelStore.getKlass()
    if (faculty && klass === 'Faculty') {
      this.setState({
        courses: faculty.faculty_courses,
        assets: faculty.faculty_assets,
        id: faculty.id,
        start: new window.ODate(faculty.start),
        end: new window.ODate(faculty.end),
        name: faculty.name,
        quiz: faculty.most_quiz,
        total_assets: faculty.total_assets,
        resource: faculty.most_resource,
        assignment: faculty.most_assignment,
        activity: faculty.most_activity,
        onlineCourseCount: faculty.most_online_course_count,
        onlineCourseDuration: faculty.most_online_course_duration,
        bbOnlineCourseCount: faculty.most_bb_online_course_count,
        bbOnlineCourseDuration: faculty.most_bb_online_course_duration,
        meetingHistogram: faculty.meetings,
        meetingCount: faculty.meetings_count,
        bbMeetingHistogram: faculty.bb_meetings,
        bbMeetingCount: faculty.bb_meetings_count,
        inactives: faculty.inactives[0],
        all:  faculty.inactives[1],
      });
    }
    //console.log(faculty)    
  }

  pageAfterIn() {
    setTimeout(
      () => this.loadCalender(), 
      5000
    );
  }

  loadCalender(){
    const self = this;
    const app = self.$f7;
    console.log(new Date())

    app.calendar.create({
      inputEl: '#faculty-start',
      value: [self.state.start],
      closeOnSelect: true,
      firstDay: 6,
      weekendDays: [4, 5],
      monthNames: ['فروردين', 'ارديبهشت', 'خرداد', 'تیر', 'مرداد', 'شهریور', 'مهر', 'آبان', 'آذر', 'دی', 'بهمن', 'اسفند'],
      dayNames: ['یکشنبه', 'دوشنبه', 'سه شنبه', 'چهارشنبه', 'پنج‌شنبه', 'جمعه', 'شنبه'],
      dayNamesShort: ['یک', 'دو', 'سه ', 'چهار', 'پنج‌', 'جمعه', 'شنبه'],
      on: {
        closed: function (c) {
          var start = new Date(c.value[0].a).valueOf()/ 1000
          self.setState({ start:start });
        }
      }
    });


    app.calendar.create({
      inputEl: '#faculty-end',
      closeOnSelect: true,
      value: [self.state.end],
      firstDay: 6,
      weekendDays: [4, 5],
      monthNames: ['فروردين', 'ارديبهشت', 'خرداد', 'تیر', 'مرداد', 'شهریور', 'مهر', 'آبان', 'آذر', 'دی', 'بهمن', 'اسفند'],
      dayNames: ['یکشنبه', 'دوشنبه', 'سه شنبه', 'چهارشنبه', 'پنج‌شنبه', 'جمعه', 'شنبه'],
      dayNamesShort: ['یک', 'دو', 'سه ', 'چهار', 'پنج‌', 'جمعه', 'شنبه'],
      on: {
        closed: function (c) {
          var end = new Date(c.value[0].a).valueOf()/ 1000
          self.setState({ end: end }, () => {
            self.submit()
          });
        }
      }

    });

  }




  render() {
    const { 
      id, courses, assets, total_assets , quiz, name,
      resource, assignment, onlineCourseCount, onlineCourseDuration, 
      activity, bbOnlineCourseCount, bbOnlineCourseDuration, meetingHistogram,
      bbMeetingHistogram, meetingCount, bbMeetingCount, inactives, all
    
    } = this.state;
    return (
      <Page onPageAfterIn={this.pageAfterIn.bind(this)}>
        <Navbar title={dict.faculties} backLink={dict.back} />
        <FacultyShow 
          id={id} courses={courses} name={name} assignment={assignment} 
          onlineCourseCount={onlineCourseCount} activity={activity} 
          onlineCourseDuration={onlineCourseDuration} quiz={quiz} 
          resource={resource} assets={assets} total_assets={total_assets}
          bbOnlineCourseCount={bbOnlineCourseCount}
          bbOnlineCourseDuration={bbOnlineCourseDuration}
          meetingHistogram={meetingHistogram}
          bbMeetingHistogram={bbMeetingHistogram}
          meetingCount={meetingCount}
          bbMeetingCount={bbMeetingCount}
          inactives={inactives}
          all={all}
          />
      </Page>
    );
  }
}
