import React from "react";
import { List, ListItem, Card, CardHeader, CardContent, CardFooter, BlockTitle } from 'framework7-react';
import { dict } from '../../Dict';
import crypto from 'crypto-js';
import { Chart } from 'react-charts'
import { color } from "d3";

import { defaults, Bar, Pie } from 'react-chartjs-2';

defaults.global.defaultFontFamily = 'iransans';

const ProfileShow = (props) => {

  function keys() {
    var result = []
    Object.keys(props.course.modules).map((key) => {
      switch (key) {
        case "1":
          result.push('assign')
          break;
        case "2":
          result.push('assignment')
          break;
        case "3":
          result.push('book')
          break;
        case "4":
          result.push('chat')
          break;
        case "5":
          result.push('choice')
          break;
        case "6":
          result.push('data')
          break;
        case "7":
          result.push('feedback')
          break;
        case "8":
          result.push('folder')
          break;
        case "9":
          result.push('forum')
          break;
        case "10":
          result.push('glossary')
          break;
        case "11":
          result.push('imscp')
          break;
        case "12":
          result.push('label')
          break;
        case "13":
          result.push('lesson')
          break;
        case "14":
          result.push('lti')
          break;
        case "15":
          result.push('page')
          break;
        case "16":
          result.push('quiz')
          break;
        case "17":
          result.push('resource')
          break;
        case "18":
          result.push('scorm')
          break;
        case "19":
          result.push('survey')
          break;
        case "20":
          result.push('url')
          break;
        case "21":
          result.push('wiki')
          break;
        case "22":
          result.push('workhop')
          break;
        case "31":
          result.push('scheduler')
          break;
        case "32":
          result.push('hvp')
          break;
        case "33":
          result.push('offlinequiz')
          break;
        default:
          break;
      }

    })
    return result
  }

  function pieData() {

  }

  function pie() {
    var data = {
      labels:
        keys()
      ,
      datasets: [{
        data: Object.keys(props.course.modules).map((key => {
          var value = props.course.modules[key]
          if (key == 9) {
            value = props.course.modules[key] - 3
          }
          if (key == 12) {
            value = props.course.modules[key] - 1
          }
          if (key == 4) {
            value = props.course.modules[key] - 1
          }
          return value
        }))

        ,
        backgroundColor: [
          '#003f5c',
          '#2f4b7c',
          '#665191',
          '#a05195',
          '#d45087',
          '#f95d6a', '#ff7c43', '#ffa600', '#944dab', '#67f71d', '#650512', '#2f5561', '#e4ae5c'
        ]
      }]
    };
    return data
  }

  function values() { }

  function stViews() {
    var result = []
    if (props.course.student_view_histogram && props.course.student_view_histogram.length > 0) {
      var colors = ['#ff6384', '#36a2eb', '#cc65fe', '#ffce56', '#20f08b', '#944dab', '#67f71d', '#650512', '#2f5561', '#e4ae5c']
      var d = []
      props.course.student_view_histogram.map((v) => {
        d.push({ x: new window.ODate(v.day).toLocaleDateString("fa-IR"), y: v.count })
      })
      result.push({
        label: dict.count,
        backgroundColor: colors[Math.floor(Math.random() * 10)],
        borderColor: 'blue',
        data: d
      })
    }

    return result
  }

  function teViews() {
    var result = []
    if (props.course.teacher_view_histogram && props.course.teacher_view_histogram.length > 0) {
      var colors = ['#ff6384', '#36a2eb', '#cc65fe', '#ffce56', '#20f08b', '#944dab', '#67f71d', '#650512', '#2f5561', '#e4ae5c']
      var d = []
      props.course.teacher_view_histogram.map((v) => {
        console.log(new window.ODate(v.day).toLocaleDateString("fa-IR"))
        d.push({ x: new window.ODate(v.day).toLocaleDateString("fa-IR"), y: v.count })
      })
      result.push({
        label: dict.count,
        backgroundColor: colors[Math.floor(Math.random() * 10)],
        borderColor: 'blue',
        data: d
      })
    }

    return result
  }

  function series() {
    var result = []
    if (props.course.meetings && props.course.meetings.length > 0) {
      var colors = ['#ff6384', '#36a2eb', '#cc65fe', '#ffce56', '#20f08b', '#944dab', '#67f71d', '#650512', '#2f5561', '#e4ae5c']
      var d = []
      props.course.meetings.map((v) => {
        d.push({ x: new window.ODate(v.end_time).toLocaleDateString("fa-IR"), y: v.duration })
      })
      result.push({
        label: dict.duration,
        backgroundColor: colors[Math.floor(Math.random() * 10)],
        borderColor: 'blue',
        data: d
      })
    }

    return result

  }

  function bbseries() {
    var result = []
    if (props.course.bb_meetings && props.course.bb_meetings.length > 0) {
      var colors = ['#ff6384', '#36a2eb', '#cc65fe', '#ffce56', '#20f08b', '#944dab', '#67f71d', '#650512', '#2f5561', '#e4ae5c']
      var d = []
      props.course.bb_meetings.map((v) => {
        d.push({ x: new window.ODate(v.start_time).toLocaleDateString("fa-IR"), y: v.duration })
      })
      result.push({
        label: dict.duration,
        backgroundColor: colors[Math.floor(Math.random() * 10)],
        borderColor: 'blue',
        data: d
      })
    }

    return result

  }

  function attendanceHeader() {
    var cols = [<td>{dict.name}</td>]
    var i = 0
    if (props.course.students && props.course.students.length > 0) {
      props.course.asset_sessions.map((session) => {
        i = i + 1
        var r = props.course.attendances.filter((item) => item.asset_id === session.asset_id)
        if (r !== undefined && r.length > 1) {
          cols.push(<td style={{ textAlign: 'center' }}>{session.asset_id}</td>)

        }


      })
    }
    return cols
  }

  function attendance() {
    var result = []
    if (props.course.students && props.course.students.length > 0) {
      props.course.students.map((student) => {
        //result.push(<tr>)
        var cols = []
        var members = 0
        props.course.asset_sessions.map((session) => {
          var x = props.course.attendances.filter((item) => item.asset_id === session.asset_id)
          if (x !== undefined && x.length > 1) {
            var m = props.course.max.filter((item) => item.asset_id === session.asset_id)
            var r = props.course.attendances.filter((item) => item.uid === student.uid && item.asset_id === session.asset_id)
            var cl = '#eee'
            if (r && r.length > 0) {


              var duration = r[0].duration
              var max = m[0].max
              var ratio = (duration / max).toFixed(2)


              if (ratio > 0.8) { var cl = '#026b23' }
              if (ratio > 0.6 && ratio <= 0.8) { var cl = '#018c2d' }
              if (ratio > 0.4 && ratio <= 0.6) { var cl = '#03a838' }
              if (ratio > 0.2 && ratio <= 0.4) { var cl = '#04d146' }
              if (ratio > 0 && ratio <= 0.2) { var cl = '#26e053' }
              members += 1
            }
            cols.push(<td style={{ backgroundColor: cl, border: '1px solid #eee' }}>{ratio}</td>)
          }

        })
        //console.log(members)

        result.push(<tr><td>{student.name} ({student.uid})</td>{cols}</tr>)
        //  result.push()
      })
    }
    return result
  }

  function starts(item) {
    var result = []
    if (props.course.course_percentile) {

      var rank = props.course.course_percentile[item]
      console.log('rank', rank)
      switch (true) {
        case rank == 0:
          result = onStars(0)
          break;
        case rank > 0 && rank < 0.20:
          result = onStars(1)
          break;
        case rank >= 0.20 && rank < 0.40:
          result = onStars(2)
          break;
        case rank >= 0.40 && rank < 0.60:
          result = onStars(3)
          break;
        case rank >= 0.60 && rank < 0.80:
          result = onStars(4)
          break;
        case rank >= 0.80:
          result = onStars(5)
          break;
      }
    }
    return result
  }

  function onStars(i) {
    var result = []
    for (var j = 0; j < i; j++) {
      result.push(<i class="fa fa-star" aria-hidden="true"></i>)
    }
    for (var j = 0; j < 5 - i; j++) {
      result.push(<i class="fa fa-star-o" aria-hidden="true"></i>)
    }
    return result
  }


  if (props.course) {
    return (
      <React.Fragment>
        <List className='fs-12'>
          <ListItem
            key={'course-show' + props.course.id}
            title={dict.title + ': ' + props.course.title + ' (' + props.course.serial + ')'}
            after=''>
          </ListItem>

          <ListItem
            key={'course-teacher' + props.course.id}
            title={dict.teacher + ': ' + props.course.teacher}
            after=''>
          </ListItem>
          {/*
          <ListItem
            key={'course-meeting' + props.course.id}
            title={dict.number_of_meetings + ': ' + props.course.number_of_meetings}
            after=''>
          </ListItem>

          <ListItem
            key={'course-avarage' + props.course.id}
            title={dict.avarage + ': ' + props.course.avarage}
            after=''>
</ListItem> */}
          {starts('resources').length > 0 &&
            <ListItem
              key={'course-resources' + props.course.id}
              title={dict.resources_rank}
              after={starts('resources')}>
            </ListItem>
          }

          {starts('exams').length > 0 &&
            <ListItem
              key={'course-exams' + props.course.id}
              title={dict.exams_rank}
              after={starts('exams')}>
            </ListItem>
          }

          {starts('exercises').length > 0 &&
            <ListItem
              key={'course-resources' + props.course.id}
              title={dict.exercises_rank}
              after={starts('exercises')}>
            </ListItem>
          }

          {starts('session_durations').length > 0 &&
            <ListItem
              key={'course-session_durations' + props.course.id}
              title={dict.session_durations}
              after={starts('session_durations')}>
            </ListItem>
          }

          {starts('student_view').length > 0 &&
            <ListItem
              key={'course-student_view' + props.course.id}
              title={dict.student_view}
              after={starts('student_view')}>
            </ListItem>
          }

          {starts('teacher_view').length > 0 &&
            <ListItem
              key={'course-teacher_view' + props.course.id}
              title={dict.teacher_view}
              after={starts('teacher_view')}>
            </ListItem>
          }
          {/*
        <ListItem
            key={'course-meeting' + props.course.id}
            title={dict.number_of_bb_meetings + ': ' + props.course.number_of_bb_meetings}
            after=''>
          </ListItem>

         <ListItem
            key={'course-avarage' + props.course.id}
            title={dict.bb_avarage + ': ' + props.course.bb_avarage}
            after=''>
</ListItem>

          <ListItem
            key={'course-moodle' + props.course.id}
            title={<a className='external' target="_blank" href={'https://elearn5.ut.ac.ir/course/view.php?id=' + props.course.mid}>{dict.link}</a>}
            after=''>
          </ListItem>

          <ListItem
            key={'course-access' + props.course.id}
            title={<a className='external' target="_blank" href={'/#!/courses/' + props.course.uuid}>{dict.access_link}</a>}
            after=''>
          </ListItem>*/}



        </List>



        <Card>
          <CardHeader></CardHeader>
          <CardContent >
            <Pie data={pie()} />
          </CardContent>
        </Card>

        {teViews().length > 0 &&
          <Card>
            <CardHeader>{dict.teacher_views}</CardHeader>
            <CardContent className='h-200'>
              <Bar
                data={{ datasets: teViews() }}
                options={{
                  maintainAspectRatio: false,
                  showTooltips: false,
                  responsive: true,
                  legend: {
                    display: false,
                  },
                  scales: {
                    xAxes: [{
                      barThickness: 5,
                      type: 'time',
                      bounds: 'ticks',
                      beginAtZero: true,
                      time: {
                        unit: "day",
                        unitStepSize: 1,
                        displayFormats: {
                          day: 'MM/D'
                        },
                      },
                      scaleLabel: {
                        display: true,
                        labelString: dict.date
                      }
                    }]
                  }
                }
                }
              />
            </CardContent>
            <CardFooter></CardFooter>
          </Card>}

        {stViews().length > 0 &&
          <Card>
            <CardHeader>{dict.student_views}</CardHeader>
            <CardContent className='h-200'>
              <Bar
                data={{ datasets: stViews() }}
                options={{
                  maintainAspectRatio: false,
                  showTooltips: false,
                  responsive: true,
                  legend: {
                    display: false,
                  },
                  scales: {
                    xAxes: [{
                      barThickness: 5,
                      type: 'time',
                      bounds: 'ticks',
                      beginAtZero: true,
                      time: {
                        unit: "day",
                        unitStepSize: 1,
                        displayFormats: {
                          day: 'MM/D'
                        },
                      },
                      scaleLabel: {
                        display: true,
                        labelString: dict.date
                      }
                    }]
                  }
                }
                }
              />
            </CardContent>
            <CardFooter></CardFooter>
          </Card>
        }




        <Card>
          <CardHeader>{dict.adobe_meetings}</CardHeader>
          <CardContent className='h-200'>
            <Bar
              data={{ datasets: series() }}
              options={{
                maintainAspectRatio: false,
                showTooltips: false,
                //responsive: true,
                scales: {
                  xAxes: [{
                    barThickness: 5,
                    type: 'time',
                    bounds: 'ticks',
                    time: {
                      unit: "day",
                      unitStepSize: 1,
                      displayFormats: {
                        day: 'MM/D'
                      },
                    },
                    scaleLabel: {
                      display: true,
                      labelString: dict.date
                    }
                  }]
                }
              }
              }
            />
          </CardContent>
          <CardFooter></CardFooter>
        </Card>

        <Card>
          <CardHeader>{dict.bb_meetings}</CardHeader>
          <CardContent className='h-200'>
            <Bar
              data={{ datasets: bbseries() }}
              options={{
                maintainAspectRatio: false,
                showTooltips: false,
                //responsive: true,
                scales: {
                  xAxes: [{
                    barThickness: 5,
                    type: 'time',
                    bounds: 'ticks',
                    time: {
                      unit: "day",
                      unitStepSize: 1,
                      displayFormats: {
                        day: 'MM/D'
                      },
                    },
                    scaleLabel: {
                      display: true,
                      labelString: dict.date
                    }
                  }]
                }
              }
              }
            />
          </CardContent>
          <CardFooter></CardFooter>
        </Card>

        <Card>

          <div class="data-table">
            <table>
              <thead>
                <tr>
                  {attendanceHeader()}
                </tr>
              </thead>
              <tbody>
                {attendance()}
              </tbody>
            </table>
          </div>
        </Card>

      </React.Fragment >
    )
  } else {
    return (null)
  }
}
export default ProfileShow;
